<template>
  <div class="home">
    <a-card title="数据同步配置" style="width: 1000px;margin: 10px auto;">
      <a-button
        slot="extra"
        type="primary"
        shape="circle"
        icon="save"
        title="保存"
        @click="save"
      ></a-button>
      <monaco-editor
        class="editor"
        v-model="code"
        language="yaml"
        theme="vs-dark"
      ></monaco-editor>
    </a-card>
  </div>
</template>
<script>
import MonacoEditor from "vue-monaco";

export default {
  components: {
    MonacoEditor
  },
  data() {
    return {
      code: ""
    };
  },
  methods: {
    save() {
      this.$axios.post("/sync/config", { config: this.code }).then(res => {
        if (res.data.success === true) {
          this.$message.info("修改成功");
        } else {
          this.$message.error("修改失败");
        }
      });
    }
  },
  created() {
    this.$axios.get("/sync/config").then(res => {
      if (res.data.success === true) {
        this.code = res.data.data;
      } else {
        this.$message.error("加载数据同步配置出错");
      }
    });
  }
};
</script>
<style>
.editor {
  width: 800px;
  height: 600px;
  margin: 0 auto;
  border: 2px solid grey;
}
</style>
